import { RingVolume } from '@material-ui/icons'
import { useRouterContext } from '@speedlo/hooks'
import React, { useCallback } from 'react'

import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

interface IProps {}

export const CallMeButton: React.FC<IProps> = props => {
  const { history } = useRouterContext()
  const makeCall = useCallback(
    (ev: React.MouseEvent<HTMLButtonElement>) => {
      const phone = window.prompt(
        'Zadej telefonní číslo, které ti bude volat (bez předvolby)',
      )
      if (phone === null) {
        return
      }
      let eventId: MaybeID = null
      if (ev.shiftKey) {
        eventId = window.prompt('A teď ještě eventId, jestli víš co děláš ;)')
      }
      history.push({
        pathname: `/order/create`,
        state: {
          eventId,
          phone,
        },
      })
    },
    [history],
  )

  if (process.env.REACT_APP_STAGE === 'prod') {
    return null
  }

  return (
    <TouchButton
      text="Zavolej mi [DEV]"
      icon={RingVolume}
      color={Colors.turquoise}
      onClick={makeCall}
    />
  )
}
